#root {
   padding:0 10px;
   margin:0 auto;
   box-sizing:border-box;
}
html {
   background-repeat:no-repeat;
   background-size:cover;
   height:100vh;
}
.uk-label {
   width:50px;
   vertical-align:1px;
   position:absolute;
   top:8px;
   left:10px;
   font-family:'Space Mono', monospace;
}
.uk-container {
   font-size:1.1em;
   overflow-y:auto;
}
#media-container {
   position:relative;
   height:54vh;
   background:radial-gradient(white, #fafafa);
   margin-bottom:5px;
   box-sizing:border-box;
   padding:0;
   text-align:center;
   overflow:hidden;
   touch-action:none;
}
#steps-container {
   height:42vh;
   padding-left:5px;
   padding-right:5px;
   padding-top:3px;
   overflow-y:scroll;
}
#steps-container::-webkit-scrollbar {
   width:8px;
   box-shadow:inset 0 0 6px rgba(0,0,0,0.3);
   border-radius:5px;
}
#steps-container::-webkit-scrollbar-thumb {
   background-color:#aaccdd;
   border-radius:5px;
}
.uk-card {
   min-height:12vh;
   border-top-right-radius:5px;
   border-bottom-right-radius:5px;
   cursor:pointer;
   box-shadow:0 0 3px #aaa;
   text-align:justify;
   color:black;
   font-family:'Hind Madurai', sans-serif;
   font-size:1em;
   border-bottom:2px inset lightblue;
}
.step-text {
   margin-bottom:0px;
   margin-left:0px;
}
.MathJax:focus, .mjx-chtml:focus, .MathJax_SVG:focus {
   outline:none;
}
.uk-button {
   border-radius:2px;
   margin-bottom:10px;
   font-family:'Space Mono', monospace;
}
.arrow, .reparrow, .rewardLearn, .rewardTest {
   position:absolute;
   display:flex;
   align-items:center;
   justify-content:center;
   width:3vh;
   height:3vh;
   top:5px;
   right:5px;
   box-shadow:0 0 2px #ccc;
   border-radius:2px;
   opacity:0.8;
   box-sizing:border-box;
   padding:15px;
}
.rewardLearn, .rewardTest {
   right:calc(7vh + 20px);
   width:6vh;
   padding-top:17px;
   padding-bottom:13px;
   color:white;
   background-color:#85BB65;
}
.rewardTest {
   right:calc(3vh + 17px);
}
.rewardHidden {
   display:none;
}
.reparrow {
   right:calc(3vh + 17px);
   padding-top:17px;
   padding-bottom:13px;
}
.reward {
   font-size:0.85em;
   right:calc(7vh + 20px);
   width:6vh;
   padding-top:17px;
   padding-bottom:13px;
   color:white;
   background-color:#85BB65;
}
.clckpnt {
   cursor:pointer;
}
.coord-input {
   width:35%;
   height:100%;
   border-radius:2px;
   border:none;
   outline:none;
   background-color:darkgreen;
   color:white;
   text-align:center;
   font-family:sans-serif;
   font-size:1.1em;
   padding-left:5px;
   text-align-last:center;
}
.coord-pair {
   min-width:40%;
   min-height:15%;
   position:absolute;
   opacity:0;
}
#reflectQuestion {
   border-top-left-radius:4px;
   border-bottom-left-radius:4px;
   font-family:'Hind Madurai', sans-serif;
}
#soundicon, #talkicon {
   position:absolute;
   bottom:5px;
   right:10px;
   width:4vh;
   height:4vh;
   cursor:pointer;
}
.sidenav {
  height:100%;
  width:0;
  position:fixed;
  z-index:1;
  top:0;
  left:0;
  background-color:#4499cc;
  overflow-x:hidden;
  overflow-y:scroll;
  padding-top:60px;
  animation-timing-function:ease-in-out;
  animation-duration:0.5s;
}
.sidenav span {
  padding:8px 8px 8px 32px;
  text-decoration:none;
  color:#818181;
  display:block;
}
.sidenav span:hover {
  color:#f1f1f1;
}
.sidenav .closebtn {
  position:absolute;
  top:0;
  right:25px;
  font-size:36px;
  margin-left:50px;
  cursor:pointer;
}
@media screen and (max-height: 450px) {
  .sidenav {padding-top:15px;}
  .sidenav a {font-size:18px;}
}
#drawerToggle {
   position:fixed;
   display:flex;
   align-items:center;
   justify-content:center;
   top:10%;
   left:0;
   height:5vh;
   width:4vh;
   cursor:pointer;
   z-index:1;
   box-shadow:0 0 3px #ccc;
   border-top-right-radius:3px;
   border-bottom-right-radius:3px;
   font-size:1.5em;
   padding-bottom:5px;
}
.sideImg {
   display:block;
   margin-top:6%;
   margin-left:8%;
   width:75%;
   box-shadow:0 0 3px #fff;
}
figcaption {
   color:white;
   font-size:0.8em;
   margin-top:5px;
   margin-left:8%;
}
#interactive {
   height:100%;
   width:100%;
}
#openModal, #bankModal {
   position:fixed;
   display:flex;
   align-items:center;
   justify-content:center;
   padding:0;
   margin:0;
   top:0px;
   right:10px;
   width:35px;
   height:18px;
   outline:none;
   border:none;
   border-bottom-left-radius:5px;
   border-bottom-right-radius:5px;
   text-align:center;
   font-family:'Hind Madurai';
   color:white;
   cursor:pointer;
   z-index:1;
   box-shadow:0 0px 3px #fff;
}
#bankModal {
   display:inline-block;
   box-sizing:border-box;
   padding-left:5px;
   padding-bottom:3px;
   right:55px;
   color:navy;
   width:85px;
   text-align:left;
   background-color:transparent;
   overflow:hidden;
}
#socketID, #nickname {
   height:5vh;
   text-align:center;
   font-family:sans-serif;
   font-size:1.25em;
}
.userbutton, .banbutton {
   position:absolute;
   left:0px;
   width:100%;
   height:30px;
   border:none;
   text-align:center;
   background-color:#ffffff;
   box-sizing:border-box;
   padding-left:5.5px;
   padding-bottom:1px;
   outline:none;
   z-index:1000;
}
.banbutton {
   background-color:lightblue;
   cursor:pointer;
}
#coverCanvas {
   position:fixed;
   top:0;
   left:0;
   width:100%;
   height:100vh;
   background-color:rgba(0, 0, 0, 0.2);
   display:none;
   z-index:999;
}
.choice-button {
   align-items:center;
   height:45px;
   width:100%;
   color:black;
   font-size:0.9em;
   font-family:'Hind Madurai';
   outline:none;
   background-color:transparent;
   font-weight:900;
   border-radius:5px;
   margin-top:1%;
}
.choiceA {
   border:3px double #33ccff;
   cursor:pointer;
   margin-top:3%;
}
.choiceB {
   border:3px double #8a8ab1;
   cursor:pointer;
}
.choiceC {
   border:3px double #33cc33;
   cursor:pointer;
}
.choiceD {
   border:3px double #cc99cc;
   cursor:pointer;
}
#testNumber {
   position:absolute;
   display:flex;
   align-items:center;
   justify-content:center;
   width:5vh;
   height:5vh;
   background-color:navy;
   top:-1px;
   right:-1px;
   font-size:1.25em;
   color:white;
   box-sizing:border-box;
   padding-bottom:3px;
   border-bottom-left-radius:4px;
}
@-webkit-keyframes hvr-bob {
  0% {
    -webkit-transform: translateY(-8px);
    transform: translateY(-8px);
  }
  50% {
    -webkit-transform: translateY(-4px);
    transform: translateY(-4px);
  }
  100% {
    -webkit-transform: translateY(-8px);
    transform: translateY(-8px);
  }
}
@keyframes hvr-bob {
  0% {
    -webkit-transform: translateY(-8px);
    transform: translateY(-8px);
  }
  50% {
    -webkit-transform: translateY(-4px);
    transform: translateY(-4px);
  }
  100% {
    -webkit-transform: translateY(-8px);
    transform: translateY(-8px);
  }
}
@-webkit-keyframes hvr-bob-float {
  100% {
    -webkit-transform: translateY(-8px);
    transform: translateY(-8px);
  }
}
@keyframes hvr-bob-float {
  100% {
    -webkit-transform: translateY(-8px);
    transform: translateY(-8px);
  }
}
.hvr-bob {
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);

  -webkit-animation-name: hvr-bob-float, hvr-bob;
  animation-name: hvr-bob-float, hvr-bob;
  -webkit-animation-duration: .3s, 1.5s;
  animation-duration: .3s, 1.5s;
  -webkit-animation-delay: 0s, .3s;
  animation-delay: 0s, .3s;
  -webkit-animation-timing-function: ease-out, ease-in-out;
  animation-timing-function: ease-out, ease-in-out;
  -webkit-animation-iteration-count: 1, infinite;
  animation-iteration-count: 1, infinite;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
  -webkit-animation-direction: normal, alternate;
  animation-direction: normal, alternate;
}
#userSideBar {
   box-shadow:0 4px 10px #ccc;
}
.wrong-answer {
   background-color:red;
}
.meter {
  height:18px;
  position:fixed;
  top:-1px;
  left:0;
  width:calc(100vw - 145px);
  background:rgba(128, 128, 128, 0.2);
  border-bottom-right-radius:20px;
  border-top-right-radius:20px;
}
.meter > span {
	display:block;
	height:100%;
  width:0%;
	border-top-right-radius:8px;
	border-bottom-right-radius:8px;
	background-color:rgba(133,187,101,0.5);
	position:relative;
	overflow:hidden;
}
